import AbstractSmartComponent from '../AbstractSmartComponent'
import Link from '../atoms/Link'
import { handleClickLink } from '../../../utils/common'

const createLinkMarkup = ({url, label, className}) => {
  const link = new Link({url, label, className})

  return link.getTemplate();
}

const createHeaderTemplate = (location) => {
  return (
    `<header class="header">
      <nav class="container header__container">
        <ul class="header__list">
          <li class="header__item">
            ${ createLinkMarkup({
              url: `/portfolio/`,
              label: `Обо мне`,
              className: `header__link ${location === `/portfolio/` ? `header__link--active` : ``}`
            })}
          </li>
          <li class="header__item">
            ${ createLinkMarkup({
              url: `/portfolio/projects`,
              label: `Мои проекты`,
              className: `header__link ${location === `/portfolio/projects` ? `header__link--active` : ``}`
            })}
          </li>
          <li class="header__item">
            ${ createLinkMarkup({
              url: `/portfolio/about`,
              label: `Технические навыки`,
              className: `header__link ${location === `/portfolio/about` ? `header__link--active` : ``}`
            })}
          </li>
        </ul>
      </nav>
    </header>`
  )
}

class Header extends AbstractSmartComponent {
  constructor () {
    super();
    this._location = window.location.pathname;
  }

  _watchPopState () {
    window.addEventListener('popstate', evt => {
      this.rerender();
    })
  }

  _applyRouterLinkHandlers () {
    const routerLinks = document.querySelectorAll('[data-router-link]');
    [...routerLinks].forEach(node => {
      node.addEventListener('click', evt => {
        evt.preventDefault();
        const route = evt.target.dataset.routerLink
        this._location = route;

        handleClickLink(route);
      })
    })
  }

  recoverListeners () {
    this._watchPopState();
    this._applyRouterLinkHandlers();
  }

  getTemplate () {
    return createHeaderTemplate(this._location);
  }
}

export default Header
